import React, { useState, useEffect, useCallback } from "react";
import MyModal from "../../util/WarnModal";

import { Card, Icon, Col, Row, Divider } from "antd";
import Style from "./style.module.css";
import CreateModal from "./CreateModal";
import AddButtons from "./AddButtons";
import ProjectMember from "./ProjectMember";
import ProjectDesc from "./ProjectDesc";
import { clearErr } from "../../../action/resource";

import { shallowEqual, useSelector, useDispatch } from "react-redux";
import ProjectSprintActivity from "./ProjectSprintActivity";
import ProjectStories from "./ProjectStories";
import useTitle from "../../util/useTitle";
import ProjectAdmin from "../../util/ProjectAdmin";
const ProjectDetails = props => {
  useTitle("project detail");
  const [visible, setVisible] = useState(false);
  const err = useSelector(state => state.err, shallowEqual);

  const dispatch = useDispatch();
  const clearErrCallback = useCallback(() => dispatch(clearErr()), [dispatch]);

  useEffect(() => {
    if (Object.keys(err).length) {
      MyModal({ data: err });
      clearErrCallback();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [err]);

  return (
    <div>
      <CreateModal
        projectId={props.match.params.projectId}
        visible={visible}
        setVisible={setVisible}
      />

      <Row type="flex" justify="space-around" style={{ margin: "0" }}>
        <Col
          style={{
            alignItems: "flex-start",
            // border: "1px solid #e0e0e0",
            margin: "0px ",
            borderRadius: "8px"
          }}
          xl={19}
          lg={17}
          xs={24}
        >
          <Card>
            <Row>
              <Col lg={18} xs={24}>
                <ProjectDesc projectId={props.match.params.projectId} />
              </Col>
              <ProjectAdmin>
                <AddButtons projectId={props.match.params.projectId} />
              </ProjectAdmin>
            </Row>
            <ProjectSprintActivity projectId={props.match.params.projectId} />
            <Divider>User Story</Divider>

            <div className={Style.usBoxContainer}>
              <ProjectAdmin>
                <div className={Style.usBox} onClick={() => setVisible(true)}>

                  <Icon
                    type="plus-circle"
                    theme="twoTone"
                    twoToneColor="#d9d9d9"
                    style={{
                      fontSize: "5em",
                      textAlign: "center",
                      margin: " auto",
                      width: '100%',
                      backgroundColor: 'transparent'
                    }}
                  />

                </div>
              </ProjectAdmin>
              <ProjectStories projectId={props.match.params.projectId} />
            </div>
          </Card>
        </Col>
        <Col xl={4} lg={6} md={24} sm={24}>
          <Card>
            <ProjectMember projectId={props.match.params.projectId} />
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default ProjectDetails;
